.start-window {
	grid-column: 9/ 30;
	grid-row: 1/5;
	position: absolute;
	left: 0;
	top: 45%;
	left: 50%;
	display: block;
	transform: translate(-50%, -50%);
	width: 388px;
	height: auto;
	background-color: white;
	border: 1px hsla(300, 4%, 89%, 0.08) solid;
	border-radius: 8px;
	display: flex;
	flex-direction: column;
	justify-content: start;
	align-items: center;
	z-index: 99;
	@include responsive(700) {
		width: 342px;
	}
	@include responsive(500) {
		width: 300px;
	}
	@include responsive(370) {
		width: 272px;
	}
	& h1 {
		margin-top: 42px;
		margin-bottom: 36px;
		color: black;
		font-size: 30px;
		font-weight: 500;
		@include responsive(700) {
			font-size: 24px;
		}
		@include responsive(500) {
			font-size: 22px;
		}
		@include responsive(370) {
			font-size: 18px;
		}
	}
	& span {
		font-size: 18px;
		margin-bottom: 18px;
		font-weight: 300;
		@include responsive(500) {
			font-size: 13px;
		}
	}
	&:last-child {
		font-weight: 500;
	}
}

.btn-start {
	width: 120px;
	height: 40px;

	margin-top: 24px;
	margin-bottom: 42px;
	background-color: #ffef29;
	border: 2px #ffef29 solid;
	color: black;
	border-radius: 8px;
	font-size: 16px;
	font-weight: 400;
	text-decoration: inherit;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
	z-index: 99;
	@include responsive(700) {
		font-size: 15px;
	}
	@include responsive(500) {
		font-size: 14px;
	}
	@include responsive(370) {
		font-size: 12px;
	}
	&:hover {
		background-color: black;
		color: white;
		border: 2px rgb(255, 255, 255) solid;
	}
}
