.footer {
	width: 100%;
	height: 66px;
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 10;
	// background-image: linear-gradient(#3BAAFF, #3baaffd4);
	background-color: black;
	display: grid;
	grid-template-columns: 72px 1fr 1fr 72px;
	grid-template-rows: 1fr;
	align-items: center;
	@include responsive(600) {
		grid-template-columns: 36px 29.236% 1fr 36px;
	}
	@include responsive(500) {
		grid-template-columns: 18px 29.236% 1fr 18px;
	}
}

.line-footer {
	width: 100%;
	height: 2px;
	position: absolute;
	bottom: 100%;
	left: 0;
	background-color: hsla(300, 4%, 89%, 0.1);
}

