#logo-footer {
	width: 149px;
	height: auto;
	grid-column: 2/3;
	grid-row: 1/2;
	@include responsive(600) {
		width: 100px;
		height: auto;
	}
}

.socialsNetwork-icons {
	position: absolute;
	width: auto;
	grid-column: 3/4;
	grid-row: 1/2;
	justify-self: end;
	display: flex;
	flex-flow: row nowrap;
	& img {
		margin-left: 36px;
		width: auto;
		height: 16px;
		@include responsive(600) {
			margin-left: 28px;
			height: 10px;
		}
	}
}
